
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

import tabGroup from '@/components/group/tab'
import couponsGroup from './coupongroup'
import { addReceive,getCouponList} from '@/api/coupon'
export default {
components: {
XHeader,
Tab,
TabItem,
XProgress,
tabGroup: tabGroup,
couponsGroup: couponsGroup
},
data () {
return {
list: ['全部', '素木集 ', ' 美妆坊', '品牌馆'],
n: 0,
title: '领券中心',
ab: '我的优惠券',
CouponList: []
}
},
props: {},
mounted () {
this.getCouponList()
},
methods: {
addReceive(couponId){//2主动获取 1后台赠送
addReceive({userId:this.$store.getters.userInfo.userId,couponId:couponId,receiveType:2}).then(res=>{
this.$message({
message:res.message
})
})
},
getback () {
this.$router.go(-1)
},
getCouponList () {

getCouponList({
pageNum: 1, pageSize:20
})
.then(res => {
this.CouponList = res.data
})
},
mycoupons () {
this.$router.push('./myCoupons')
}
}
}



